* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}


body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background-color: #4492e0;
}

.container {
  background-color: white;
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  padding: 2rem;
  box-shadow: 5px 5px #388ee4;
}

.container h1 {
  font-size: 2rem;
  text-align: center;
  text-transform: capitalize;
  color: #2681dc;
}

.container form {
  margin-top: 1.5rem;
}

.container form .row {
  display: flex;
  flex-direction: column;
}

.container form .row .voices-icons {
  background-color: white;
  margin-top: -15px;
  border: 1px solid #bbb;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top: none;
  text-align: right;
  display: flex;
  align-items: center;
  padding-bottom: 5px;
  margin-left: 0px;
}

.container form .row .voices-icons i {
  cursor: pointer;
  opacity: 0.5;
  margin: 0px 10px;
}

.container form .row .voices-icons i.speak {
  color: #2681dc;
}

.container form .row .voices-icons .select-voices {
  margin-left: 5px;
}

.container form .row .select-voices {
  flex: 1;
}

.container form textarea, .container form select {
  width: 100%;
  outline: none;
  border: none;
  border-radius: 5px;
}

.container form textarea {
  border: 1px solid #bbb;
  padding: 3px 5px;
  margin-bottom: 12px;
  resize: none;
  font-size: 18px;
}

.container form select {
  height: 30px;
  display: flex;
  align-items: center;
}

.container ul {
  list-style: none;
  margin-top: 5px;
  font-size: 13px;
  max-height: 250px;
  overflow: scroll;
}

.container ul .word h2 {
  font-size: 1.5rem;
  text-transform: capitalize;
}

.container ul .word span {
  font-size: 0.8rem;
  opacity: 0.6;
}

.container ul .contain {
  margin-top: 7px;
}

.container ul .contain .details {
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  margin-top: 10px;
  padding-left: 10px;
  border-radius: 5px;
  border-left: 3px solid #2681dc;
}

.container ul .contain .details p {
  opacity: 0.6;
}

.container ul .contain .synonyms {
  padding-bottom: 3px;
}

.container ul .contain .synonyms span {
  opacity: 0.6;
  border-bottom: 0.5px dotted #2681dc;
  cursor: pointer;
}

.container ul .contain .synonyms span:hover {
  color: #2681dc;
}

::-webkit-scrollbar{
  display: none;
}